import Vue from 'vue'
import Router from 'vue-router'
import Rainbow from './components/Rainbow.vue'
import Login from './components/Login.vue'

Vue.use(Router)


export default new Router({
  routes: [{
    path: '/',
    redirect: '/login'
  }, {
    path: '/login',
    name: 'Login',
    components: {
      main: Login
    }
  }, {
    path: '/rainbow',
    name: 'rainbow',
    components: {
      main: Rainbow
    }
  }]
})
