<template>
  <div class="rainbow">
    {{ greeting }}
    <el-button type="primary" @click="generate()">生成</el-button>
    {{ sentence }}
  </div>
</template>

<script>
export default {
  name: 'Rainbow',
  data: function() {
    return {
      greeting: null,
      sentence: null
    }
  },
  created: function() {
    this.axios({
      method: "get",
      url: "/greeting/get",
    })
    .then(response => {
      if (response.status == 200) {
        this.greeting = response.data;
      } else {
        this.$message.error("未知错误");
      }
    });
  },
  methods: {
    generate() {
      this.axios({
        method: "get",
        url: "/rainbow/get",
      })
      .then(response => {
        if (response.status == 200) {
          this.sentence = response.data;
        } else {
          this.$message.error("未知错误");
        }
      });
    }
  }
}
</script>
